<template>
	<div id="staffTable">
		<div class="table_title">详情列表</div>
		<!-- 表格搜索 -->
		<div class="search-content left clearfix">
			<span class="search_title">职位</span>
			<el-select allow-create v-model="dutyName" placeholder="全部">
				<el-option label="全部" value="全部">
				</el-option>
				<el-option v-for="item in  dutyArr " :label="item" :value="item" :key="item">
				</el-option>
			</el-select>
			<span class="search_title">姓名</span>
			<el-input v-model="searchName"></el-input>
			<span class="search_title">电话号码</span>
			<el-input v-model="searchTel"></el-input>
			<el-button @click="searchMethod">搜索</el-button>
		</div>

		<!--表格列表-->
		<div class="table-list">
			<el-table border style="width: 100%" :height="tableHeight" :data="staffList" stripe>
				<el-table-column type="selection" min-width="35">
				</el-table-column>
				<el-table-column prop="employee_id" label="员工ID" sortable align="center" min-width="70">
				</el-table-column>
				<el-table-column prop="userid" label="工号" sortable align="center" min-width="70">
				</el-table-column>
				<el-table-column prop="real_name" label="姓名" align="center" min-width="90">
				</el-table-column>
				<el-table-column prop="duty" label="职务" align="center" min-width="70">
				</el-table-column>
				<el-table-column prop="department_id" label="部门" align="center" min-width="70">
				</el-table-column>
				<el-table-column prop="phone" label="手机号码" align="center" min-width="120">
				</el-table-column>
				<el-table-column prop="is_weixin" label="微信绑定" align="center" min-width="70">
				</el-table-column>
				<el-table-column prop="remark" label="备注" align="center" min-width="140">
				</el-table-column>
				<el-table-column prop="is_freeze" label="状态" sortable align="center" min-width="70">
					<template slot-scope="scope">
						<span :class="scope.row.is_freeze | statusFilter">{{scope.row.is_freeze}}</span>
					</template>
				</el-table-column>
				<el-table-column label="操作" min-width="160" align="center">
					<template slot-scope="scope">
						<span size="small" type="info" @click="handleModifyStatus(scope.row, 0)" v-if="scope.row.is_freeze==is_enable||scope.row.is_freeze==null" class="red">冻结
						</span>
						<span size="small" type="success" @click="handleModifyStatus(scope.row, 1)" v-else class="green">启用
						</span>
						<router-link :to="{path:'/staff/edit',query:{employeeId:scope.row.employee_id}}">
							<span size="small" type="text" class="blue">编辑
							</span>
						</router-link>
						<span size="small" type="text" @click="handleDelete(scope.row )" class="red">删除
						</span>
					</template>
				</el-table-column>
			</el-table>
			<!--分页弹框-->
			<div class="pagination-container">
				<el-pagination small @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="showStaffList.page" :page-sizes="[10,20,30,40]" :page-size="showStaffList.limit" layout="sizes, prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from "vue";
	import { staffDataGet } from "../api";
	import { treeMethods } from "../index";
	import { isFreeze, isWechat, isAdmin, isDelete, positionRight } from "@/config/cfg";
	export default {
		data() {
			return {
				tableHeight: 0,
				dutyName: "", //下拉框中选中的部门名称
				searchName: "", //搜索名称或者编号
				searchTel: "", //搜索电话号码
				staffList: [],
				searchList: [], //满足搜索的列表不随树的active而变化
				dutyArr: [],
				is_enable: "",
				showStaffList: {
					page: 1,
					limit: 10
				},
				total: 10
			};
		},
		created() {
			this.tableHeight = document.documentElement.clientHeight - (50 + 50 + 70 + 100);
			$(window).resize(() => {
				this.tableHeight = document.documentElement.clientHeight - (50 + 50 + 70 + 100);
			});
			this.initTableList();
		},

		computed: {
			departId() {
				return this.$store.state.staff.departId;
			},
			departInforList() {
				return this.$store.state.staff.departInforList;
			},
			pageSize() {
				return this.$store.state.staff.pageSize;
			},
			departNameSave() {
				return this.$store.state.staff.departNameSave;
			}
		},
		watch: {
			departId: function(val) {
				this.dutyName = "";
				this.searchName = "";
				this.saleTel = "";
				if(val == 0) {
					this.initTableList();
				} else {
					this.getDepartStaff();
				}
			}
		},
		methods: {
			//初始化列表
			initTableList() {
				staffDataGet.getStaffList({
					get_employee_list: 1
				}, (resp) => {
					console.log(resp)
					this.is_enable = isFreeze.code[isFreeze.NO];
					this.staffList = resp.data.employee_list;
					this.staffList = treeMethods.judgeStaffList(this.staffList, 0);
					this.searchList = treeMethods.judgeStaffList(this.staffList, 0);
					this.dutyArr = treeMethods.getDutyArr(this.staffList);
					this.total = treeMethods.getTotal(this.searchList);
					this.staffList = treeMethods.getPageList(resp.data.employee_list, this.showStaffList.limit, this.showStaffList.page);

				});
			},
			//点击树中的部门名称，列表显示对应的用工列表
			getDepartStaff() {
				if(this.departId !== 0) {
					let data = {
						get_department_employee: 1,
						department_id: this.departId
					};
					staffDataGet.getDepartStaffList(data, (resp) => {
						this.staffList = resp.data.employee_list;
					});
				}
			},
			//切换操作中的冻结启用
			handleModifyStatus(row, status) {
				row.is_freeze = status == isFreeze.NO ? isFreeze.code[isFreeze.YES] : isFreeze.code[isFreeze.NO];
				let data = {
					freeze_employee: 1,
					employee_id: row.userid,
					type: status
				};
				staffDataGet.getUserInfor(data, (resp) => {
					console.log(status)
				})
				return false;
			},
			//点击删除
			handleDelete(row) {
				let data = {
					del_employee: 1,
					employee_id: row.employee_id,
				}
				staffDataGet.getUserInfor(data, (resp) => {
					this.initTableList()
				})
				this.$notify({
					title: '成功',
					message: '删除成功',
					type: 'success',
					duration: 2000
				});
				return false;
			},
			//点击搜索
			searchMethod() {
				this.staffList = treeMethods.search(this.dutyName, this.searchName, this.searchTel, this.searchList);
			},
			//选择每页显示的条目数
			handleSizeChange(val) {
				this.showStaffList.limit = val;
				this.staffList = treeMethods.getPageList(this.searchList, this.showStaffList.limit, this.showStaffList.page);
				this.total = treeMethods.getTotal(this.searchList);
			},
			//选择当前页码
			handleCurrentChange(val) {
				this.showStaffList.page = val;
				this.total = treeMethods.getTotal(this.searchList);
				this.staffList = treeMethods.getPageList(this.searchList, this.showStaffList.limit, this.showStaffList.page);
			},
		},
		filters: {
			statusFilter(status) {
				const statusMap = ['red', 'green'];
				if(status == isFreeze.code[isFreeze.NO] || null) {
					statusMap[status] = 'green';
				} else {
					statusMap[status] = 'red';
				}
				return statusMap[status];
			}
		},
	};
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#staffTable {
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.table_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		background-color: #f6f8fc;
		padding-left: 14px;
	}
	
	.search-content {
		margin: 20px 14px 20px 0;
	}
	
	.deleteBtn.el-button {}
	
	.search_title {
		font-size: 14px;
		padding: 14px;
	}
	
	.table-list {
		height: 100%;
		background: #FFFFFF;
		padding: 14px 14px 0 14px;
	}
	
	.pagination-container {
		margin: 50px 0 50px 524px;
		font-family: MicrosoftYaHei;
		font-size: 12px;
		color: #666666;
		letter-spacing: 0.43px;
	}
	
	.pageSize {
		font-size: 12px;
		width: 70px;
		height: 34px;
		margin-right: 10px;
	}
	
	div.el-select.el-input {
		font-size: 12px;
	}
	
	.jump {
		font-size: 12px;
		margin-right: 10px;
	}
	
	.jump .el-input {
		font-size: 12px;
		width: 80px;
		height: 34px;
	}
	
	.jump .el-button {
		font-size: 12px;
		width: 30px;
		height: 34px;
		background: #F3F3F3;
	}
	
	.blue {
		font-size: 14px;
		color: #4877E7;
		margin-right: 14px;
	}
	
	.red {
		font-size: 14px;
		color: #E7487E;
		margin-right: 14px;
	}
	
	.green {
		font-size: 14px;
		color: limegreen;
		margin-right: 14px;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#staffTable {
		font-size: 0;
		.search-content {
			.el-input {
				font-size: 14px;
				width: inherit;
				margin-right: 20px;
				.el-input__inner {
					@include wh(180px, 34px);
					border-radius: 0;
				}
			}
		}
		.el-button {
			@include wh(100px, 34px);
			border: 1px solid #4877e7;
			color: #4877e7;
			border-radius: 0;
			font-size: 14px;
		}
		.el-table {
			.el-button {
				@include wh(50px, 30px);
				color: #FFFFFF;
				border: none
			}
		}
	}
</style>


